input,
textarea {
  box-sizing: border-box;
  width: 100%;
  font-size: 14px;
  background-color: $form-backround;
  color: $form-text;
  font-family: "Space Mono", sans-serif;
  outline: none;
  border: 1px solid transparent;
  border-radius: 8px;

  &:hover {
    color: $main-text-color;
    border: 1px solid $form-hover;
  }

  &:focus {
    color: $main-text-color;
    border: 1px solid $main-text-color;
  }

  &:-webkit-autofill,
  &:-webkit-autofill:focus {
    box-shadow: 0 0 0 1000px $form-backround inset;
    -webkit-text-fill-color: $main-text-color;
  }
}

.content {
  margin-bottom: 48px;

  &__text {
    padding: 14px 16px;
    margin-bottom: 16px;
  }

  &__area {
    display: flex;
    resize: none;
    padding: 14px 16px;
    line-height: 140%;
    margin-bottom: 32px;
  }

  &__button {
    width: 100%;
    height: 56px;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    border: 2px solid $button-hover-border-color;
    color: $button-text-color;
    font-family: "Space Grotesk", sans-serif;
    font-size: 16px;
    font-weight: 700;
    background-color: $main-text-color;
    transition-duration: 0.3s;

    &:hover {
      box-sizing: border-box;
      color: $main-text-color;
      border: 2px solid $button-hover-border-color;
      background: $button-hover-background;
      cursor: pointer;
    }

    &:active {
      color: $button-text-color;
      background-color: $button-active-backround;
    }
  }

  @include onTablet {
    margin-bottom: 80px;
  }

  @include onDesktop {
    margin-bottom: 0;
  }
}
